@mixin background {
  z-index: 2;
  background: var(--modal-bg-color-for-blur);
}

$blur: 10px;

#page-wrapper {
  filter: blur(0);

  body.modal-open & {
    filter: blur($blur);
  }
}

body.modal-open {
  overflow: hidden;
}

.modal-close-button {
  display: none;
  position: absolute;
  align-items: center;
  color: white;
  top: 2rem;
  right: 2rem;
  background: none;
  border: none;
  font-size: 1.3rem;
  cursor: pointer;
  .icon {
    font-size: 2rem;
  }
}

@mixin modal-open {
  opacity: 1;
  visibility: visible;
  @include background;
  animation: modalOpen 300ms ease-in-out;
  @media screen and (prefers-reduced-motion: reduce) {
    animation: none;
  }
}

@mixin modal-close {
  visibility: hidden;
  opacity: 0;
  @include background;
  animation: modalClose 300ms ease-in-out;
  @media screen and (prefers-reduced-motion: reduce) {
    animation: none;
  }
}

@keyframes modalOpen {
  0% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes modalClose {
  0% {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    background: transparent;
    visibility: hidden;
    opacity: 0;
  }
}
